:root {
    --primary-color: #4a90e2;
    --background-color: #fafafa;
    --text-color: #262626;
    --border-color: #dbdbdb;
    --shadow-color: rgba(0, 0, 0, 0.1);
  }
  
  * {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  
  body {
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif;
    background-color: var(--background-color);
    color: var(--text-color);
    line-height: 1.6;
    padding: 20px;
  }
  
  header {
    text-align: center;
    margin-bottom: 2rem;
  }
  
  h1 {
    font-size: 2rem;
    margin-bottom: 0.5rem;
  }
  
  main {
    max-width: 600px;
    margin: 0 auto;
  }
  
  form {
    background-color: white;
    padding: 20px;
    border-radius: 12px;
    box-shadow: 0 2px 12px var(--shadow-color);
  }
  
  textarea {
    width: 100%;
    min-height: 100px;
    padding: 12px;
    border: 1px solid var(--border-color);
    border-radius: 8px;
    margin-bottom: 16px;
    font-size: 16px;
    resize: vertical;
  }
  
  button {
    width: 100%;
    padding: 12px;
    background-color: var(--primary-color);
    color: white;
    border: none;
    border-radius: 8px;
    font-size: 16px;
    font-weight: 600;
    cursor: pointer;
    transition: background-color 0.2s;
  }
  
  button:hover {
    background-color: #357abd;
  }
  
  .poem-display {
    margin-top: 2rem;
    background-color: white;
    padding: 24px;
    border-radius: 12px;
    box-shadow: 0 2px 12px var(--shadow-color);
    white-space: pre-line;
  }
  
  small {
    display: block;
    text-align: center;
    margin-top: 2rem;
    color: #666;
  }
  
  small a {
    color: var(--primary-color);
    text-decoration: none;
  }
  
  small a:hover {
    text-decoration: underline;
  }
  
  @media (max-width: 480px) {
    body {
      padding: 16px;
    }
  
    h1 {
      font-size: 1.5rem;
    }
  
    form {
      padding: 16px;
    }
  }
  